.header-container {
  display: flex;
  flex-direction: column;
  height: fit-content;
  width: 100%;
  margin: 0 auto 10px auto;
  padding: 0 10px;
  box-sizing: border-box;
  top: 0;
}

.header-title {
  font-size: 1.75em;
  font-weight: bold;
  margin: 0 10px 0 10px;
  text-align: center;
  text-shadow: 0 1px 1px rgb(11, 41, 46);
}
