.options {
  font-size: 1.25em;
  height: 100%;
  width: 100%;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.content {
  margin: 20px auto;
}
.email a {
  text-decoration: none;
}
.email a:hover {
  text-decoration: none;
  color: rgb(62, 22, 22);
}
.email a:visited {
  text-decoration: none;
}
.option {
  margin: 10px 0;
}
.options-title {
  text-align: center;
  font-size: 1.5em;
}
.option-label {
  font-size: 1.2em;
}
.option-checkbox {
  font-size: 1.25em;
  cursor: pointer;
  margin: 5px 8px;
  display: inline-block;
}
#status {
  font-size: .85em;
  margin: auto;
  margin-left: 15.5%;
  text-align: center;
  opacity: .6;
  position: fixed;
}
#status2 {
  font-size: 1.05em;
  margin: auto;
  margin-left: 26%;
  text-align: center;
  opacity: .6;
  position: fixed;
}
.save-btn {
  background: rgb(255, 255, 255, .9);
  color: #46474c;
  border: 0;
  border-radius: 5px;
  padding: 6px 32px;
  font-size: 1.25em;
  font-weight: bold;
  margin: 25px auto 0 auto; 
  display: block;
  cursor: pointer;
}

input[type=checkbox] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  
  width: 15px;
  height: 15px;
  padding: 0px;

  background-clip: content-box;
  border: 1.5px solid #bbbbbb;
  background-color: #f1eff1;
  margin-right: 5px;
}
input[type=checkbox]:checked {
    background-color: #84be5d;
}

input[type=checkbox]:focus {
    outline: none !important;
}

