@font-face {
  font-family: 'Georama';
  src: local('Georama'), url(./assets/Georama.ttf) format('woff');
}

.popup-container {
  font-family: 'Georama';
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 240px;
  height: 320px;
  padding: 20px 0 5px 0;
  background: #46474c;
  color: rgb(255, 255, 255);
  margin: auto;
}
.header-title {
  color: #84be5d
}
.link, .footer-link {
  color: white;
}
.link, .footer-link {
  font-size: 1em;
  margin: 0 auto;
  text-decoration: none;
}
.link:hover,
.footer-link:hover {
  text-decoration: underline;
}
